.rec {

  &__name,
  &__price {
    font-weight: 700;
    line-height: 170%;
    color: #292929;
  }

  &__class {
    color: #292929;
    line-height: 170%;
  }

  &__picture-container {
    margin-bottom: 32px;

    display: flex;
    justify-content: center;
    gap: 24px;
  }

  &__picture {
    width: 100%;
    height: 100%;
    transition: transform 0.3s linear;

    @include onDesktop {
      width: 324px;
    }

    &:hover {
      transform: scale(1.05);
    }
  }
}
